import React from "react";
import ReactPlayer from "react-player";
import styles from "./Video.module.scss";

const Video = () => {
  return (
    <div className={styles.videoPlayer}>
      <ReactPlayer
        url="https://raw.githubusercontent.com/cththes/Social-Network-Database/master/Videos/King%20of%20the%20Hill.mp4"
        controls={true}
        width="640px"
        height="480px"
        playing={true}
        changePlaybackRate={false}
        config={{ file: { attributes: { controlsList: "noplaybackrate nodownload" } } }}
      />
    </div>
  );
};

export default Video;
